import React from 'react'
import Utils from '../Utils'
import Card from '@material-ui/core/Card'
import CardContent from '@material-ui/core/CardContent'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import Switch from '@material-ui/core/Switch'
import Button from '@material-ui/core/Button'


const classes = {
  cardStyle: {
    maxWidth: '800px',
    marginTop: '20px',
    marginRight: 'auto',
    marginLeft: 'auto',
  }
}

class Settings extends React.Component {
  state = {
    ...Utils.settings,
  }

  saveSettings = () => {
    Utils.setSettings({
      ...this.state,
    });
    window.toastr.success(' lu ci t');
  }

  render() {
    const { markReadOnOpenInbox } = this.state;

    return (
      <div style={classes.cardStyle}>
        <Card>
          <CardContent>
            <p>
              <h2>Ci t</h2><br/>
              Lu : Cc ci t sau s c lu trn trnh duyt ny. Nu bn i thit b khc, bn cn chnh li cho thit b .
            </p>
            <FormControlLabel
              control={
                <Switch
                  checked={markReadOnOpenInbox}
                  onChange={() => this.setState({ markReadOnOpenInbox: !markReadOnOpenInbox })}
                  color="primary"
                />
              }
              label={'nh du " c" khi bm vo c inbox'}
            />
            <br/>
            <br/>
            <Button
              variant='outlined'
              onClick={this.saveSettings}
            >Lu ci t</Button>
          </CardContent>        
        </Card>
      </div>
    )
  }
}

export default Settings